* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
  letter-spacing: 0.1rem;
  text-transform: capitalize;
  transition: 0.4s;
}

body {
  overflow-x: hidden;
}

.btn {
  width: 12rem;
  height: 3.4rem;
  color: #fff;
  background: #003cb3;
  font-size: 1rem;
  text-decoration: none;
  border: none;
  border-radius: 0.3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  transition: 0.5s;
  position: relative;
  margin: 1rem;
  padding: 0.5rem;
}
.btn:hover {
  background: #2755b2;
}
.btn:hover span {
  margin-left: 0.4rem;
  width: 3.2rem;
}
.btn span {
  font-size: 1.2rem;
  width: 0;
  overflow: hidden;
  transition: 0.5s;
}

.title {
  color: #000080;
  padding: 1rem;
  font-size: 3rem;
  text-align: center;
}

section {
  margin: 0;
  padding: 3rem 1rem;
}

.prudect-Cards-Container {
  display: grid;
  grid-template-columns: repeat(3, 32%);
  gap: 0.5rem;
  row-gap: 1.2rem;
}

@media (max-width: 991px) {
  section {
    padding: 1.5rem 0.5rem;
  }
}
@media (max-width: 700px) {
  .title {
    padding: 1rem;
    font-size: 2rem;
  }
  section {
    padding: 1rem 0.2rem;
  }
  .btn {
    width: 10rem;
    height: 3rem;
    font-size: 0.8rem;
    margin: 0.7rem;
    padding: 0.3rem;
  }
  .btn:hover span {
    margin-left: 0.3rem;
    width: 2.7rem;
  }
  .btn span {
    font-size: 0.9rem;
  }
  .prudect-Cards-Container {
    grid-template-columns: repeat(2, 50%);
    row-gap: 0.7rem;
  }
}
@media (max-width: 441px) {
  .title {
    padding: 0.7rem;
    font-size: 1.5rem;
  }
  .btn {
    width: 7.5rem;
    height: 2.6rem;
    font-size: 0.8rem;
    margin: 0.8rem;
  }
  .btn:hover span {
    margin-left: 0.1rem;
    width: 2.2rem;
  }
  .btn span {
    font-size: 0.7rem;
  }
  .prudect-Cards-Container {
    grid-template-columns: 100%;
  }
}
a {
  text-decoration: none;
}
a .PrudectCard {
  width: 100%;
  margin: 0.7rem;
  padding: 0.5rem;
  background: transparent;
  text-align: start;
  overflow: hidden;
  position: relative;
  top: 0;
  left: 0;
}
a .PrudectCard:hover {
  transition: 0.5s;
}
a .PrudectCard:hover .go-to-more-details {
  opacity: 1;
  width: 96%;
}
a .PrudectCard:focus {
  transition: 0.5s;
}
a .PrudectCard:focus .go-to-more-details {
  opacity: 1;
  width: 96%;
}
a .PrudectCard img {
  width: 100%;
  height: 15rem;
}
a .PrudectCard .go-to-more-details {
  top: 0.5rem;
  opacity: 0;
  width: 0;
  height: 15rem;
  display: flex;
  color: #e6f9ff;
  justify-content: center;
  align-items: center;
  font-size: 1.4rem;
  position: absolute;
  background: rgba(51, 45, 147, 0.409);
  z-index: 20;
  transition: 0.7s;
}
a .PrudectCard h1 {
  color: #000080;
  font-size: 1.2rem;
  margin: 0.5rem 0;
}
a .PrudectCard p {
  font-size: 0.9rem;
  word-spacing: 0.2rem;
  color: #777;
  line-height: 1.6rem;
}
a .PrudectCard h3 {
  color: #666;
  font-size: 1rem;
}
a .PrudectCard .more-info {
  word-spacing: 0.4rem;
  font-size: 1rem;
  color: #003cb3;
  margin: 0.5rem 0;
}

@media (max-width: 751px) {
  a .PrudectCard {
    width: 95%;
    margin: 1rem 0;
    padding: 0.3rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid #cfcece;
  }
  a .PrudectCard img {
    height: 12rem;
  }
  a .PrudectCard .go-to-more-details {
    top: 0.3rem;
    width: 96.3%;
    height: 12rem;
    font-size: 1rem;
  }
  a .PrudectCard h1 {
    font-size: 1.1rem;
    margin: 0.6rem 0;
  }
}
@media (max-width: 441px) {
  a .PrudectCard {
    width: 100%;
  }
  a .PrudectCard .go-to-more-details {
    width: 98%;
  }
}/*# sourceMappingURL=PrudectCard.css.map */